<template>
  <div class="article-wrap">
    <!-- 文章标题与分类信息 -->
    <div class="articleLable">
      <p>My Articles</p>
    </div>
    <div class="articleSort">
      <div class="sortType">
        <span
          :class="{ activeSortType: (sortType=='date') }"
          v-on:click="handleSort('date')"
        >最新发布<i
            class="fa fa-chevron-up"
            v-if="sortOrder==-1&sortType=='date'"
          ></i><i
            class="fa fa-chevron-down"
            v-if="sortOrder==1&sortType=='date'"
          ></i></span>
        <span
          :class="{ activeSortType: (sortType=='view') }"
          v-on:click="handleSort('view')"
        >最多阅读<i
            class="fa fa-chevron-up"
            v-if="sortOrder==-1&sortType=='view'"
          ></i><i
            class="fa fa-chevron-down"
            v-if="sortOrder==1&sortType=='view'"
          ></i></span>
        <span
          :class="{ activeSortType: (sortType=='love') }"
          v-on:click="handleSort('love')"
        >最多点赞<i
            class="fa fa-chevron-up"
            v-if="sortOrder==-1&sortType=='love'"
          ></i><i
            class="fa fa-chevron-down"
            v-if="sortOrder==1&sortType=='love'"
          ></i></span>
      </div>
      <!-- <div class="viewType">
        <span><i class="fa fa-list"></i></span>
        <span><i class="fa fa-table"></i></span>
      </div> -->
    </div>
    <div class="articleTags">
      <span
        class="tag chip"
        v-for="(tag,index) in tagList"
        :key="index"
        :class="{ activeTag: (tagName==tag.tagName) }"
        v-on:click="handleTag(tag.tagName)"
      >
        <i class="fa fa-tags"></i> {{ tag.tagName }} ( {{ tag.tagCount }} )
      </span>
    </div>
    <!-- 文章列表 -->
    <div class="articleList">
      <div
        class="card"
        v-for="(article,index) in articlesList"
        :key="index"
      >
        <img
          :src="'/images/'+article.img"
          alt="图片路径不对鸭"
          v-if="article.img"
        >
        <img
          src="../../assets/img/none.jpg"
          alt=""
          v-else
        >
        <div class="content">
          <div
            class="pinnedArtilce"
            v-if="article.pinned"
          ><i class="fa fa-thumb-tack"></i>置顶</div>
          <div class="title"><a v-on:click="readArticle(article)">{{article.title}}</a></div>
          <div class="info">
            <span class="date"><i class="fa fa-calendar"></i>{{article.date}}</span>
            <span
              class="tag"
              v-on:click="handleTag(article.tag)"
            ><i class="fa fa-tags"></i>{{article.tag}}</span>
          </div>
          <div
            class="detail"
            v-on:click="readArticle(article)"
          >
            <p>{{ article.pureContent }}</p>
          </div>
          <div class="info">
            <!-- <span class="comment"><i class="fa fa-comment"></i>评论 ( {{article.comment}} )</span> -->
            <span class="views"><i class="fa fa-eye"></i>浏览 ( {{article.view}} )</span>
            <span class="love">
              <span
                class="heart"
                :class="{'heartActive':(youHaveLoved.indexOf(article._id) != '-1')}"
                @click.once="loveArtilce(article,$event)"
              >
              </span>
              <span>喜欢 ( {{article.love}} )</span>
            </span>
          </div>
        </div>
      </div>
    </div>
    <!-- 分页按钮 -->
    <el-pagination
      class="pagination"
      layout="prev, pager, next"
      :page-count="articlesListLength"
      :current-page="currentPage"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      articlesList: [],
      lovingArticle: '',
      currentPage: 1,
      sortType: 'date',
      sortOrder: -1,
      tagList: '',
      tagName: '全部',
      youHaveLoved: JSON.parse(localStorage.getItem("youHaveLoved")) || [],
      articlesListLength: 0,
      articlesPerPage: 5,
    }
  },
  methods: {
    getArticleList: function () {
      axios
        .get('/apis/getArticleList', {
          params: {
            currentPage: this.currentPage,
            sortType: this.sortType,
            sortOrder: this.sortOrder,
            tagName: this.tagName,
            articlesPerPage: this.articlesPerPage
          }
        })
        .then(res => {
          this.articlesListLength = Math.ceil(res.data.articlesListLength / this.articlesPerPage)
          this.articlesList = res.data.articlesList.map(function (e) {
            e.pureContent = e.content.replace(/<\/?.+?\/?>/g, "")
            return e
          })
        })
    },
    getTagList: function () {
      axios.get('/apis/getTagList').then(res => {
        this.tagList = res.data.tags
      })
    },
    readArticle: function (e) {
      // sessionStorage.setItem("article", JSON.stringify(e))
      // sessionStorage.setItem("articleTimeout", JSON.stringify(new Date().getTime()))
      this.$router.push('/article/' + e._id)
      // this.$router.push({ path: '/article/' + e._id, params: { article: e } })
    },
    loveArtilce: function (e, event) {
      if (this.youHaveLoved.indexOf(e._id) == '-1') {
        // 点击开关，当点击一次时开关关闭，在完成动画后开关打开，避免在动画执行期间重复点击
        e.love++
        $(event.target).addClass("heartAnimation")
        // 给点击动画预留的时间缓冲
        var that = this
        setTimeout(function () {
          that.youHaveLoved.push(e._id)
          axios
            .get('/apis/loveArtilce', {
              params: {
                id: e._id
              }
            })
          localStorage.setItem("youHaveLoved", JSON.stringify(that.youHaveLoved))
        }, 800)
      }
      else {
        // this.$message.error('调皮😝你已经点过赞啦～');
        this.$notify({
          title: '调皮😝',
          message: '你已经点过赞啦～',
          type: 'warning'
        });
      }
    },
    handleCurrentChange: function (e) {
      this.currentPage = e
      this.getArticleList()
      $('html , body').animate(
        { scrollTop: $('.articleList').offset().top - 280 },
        300
      )
    },
    handleSort: function (e) {
      if (this.sortType === e) {
        this.sortOrder = -this.sortOrder
      } else {
        this.sortType = e
        this.sortOrder = -1
      }
      //   console.log(this.sortOrder)
      this.getArticleList()
    },
    handleTag: function (e) {
      this.tagName = e
      this.getArticleList()
    }
  },
  mounted () {
    this.getTagList()
    this.getArticleList()
  }
}
</script>

<style lang="scss" scoped>
@media only screen and (max-width: 1069px) {
  .article-wrap {
    padding: 0 10px;
    .card {
      width: 100% !important;
      height: auto !important;
      padding: 30px 20px 20px 20px;
      .content {
        width: 100% !important;
        height: auto !important;
      }
      img {
        display: none;
      }
      .title {
        margin: 0px 0 10px 0 !important;
      }
    }
  }
}
$card-heigh: 170px;
.article-wrap {
  max-width: 700px;
  .articleLable {
    // border-bottom: 2px solid #ccc;
    text-align: center;
    p {
      font-size: 1.5em;
      display: inline-block;
      font-size: 2.5em;
      font-weight: 800;
      padding-bottom: 10px;
      border-bottom: 5px solid #f300b4;
      margin-bottom: 20px;
    }
  }
  .articleSort {
    position: relative;
    height: 40px;
    line-height: 40px;
    font-size: 12px;
    border-bottom: 2px solid #ccc;
    margin-bottom: 5px;
    .activeSortType {
      color: rgba(34, 118, 243, 0.829);
      font-weight: 800;
    }
    .sortType {
      position: absolute;
      left: 10px;
      span {
        cursor: pointer;
        margin-right: 5px;
        &:hover {
          // text-decoration: underline;
          // transform: scale(1.2);
          color: rgba(14, 74, 163, 0.829);
        }
      }
    }
    .viewType {
      position: absolute;
      right: 0;
      > span {
        cursor: pointer;
        margin-right: 5px;
        font-size: 16px;
        color: #666;
        i:hover {
          transform: scale(1.2);
          color: #000;
        }
      }
    }
  }
  .articleTags {
    font-size: 12px;
    .tag {
      height: 30px;
      line-height: 30px;
      margin: 5px;
      cursor: pointer;
    }
    .activeTag {
      color: #e73da0;
      font-weight: 900;
    }
  }
  .articleList {
    .card {
      &:hover {
        background-color: #fdfbf4;
      }
      display: flex;
      width: 700px;
      height: auto;
      border-bottom: 1px solid #bfab86;
      border-radius: 15px;
      img {
        // 长宽是父元素的80%，外边距是10%
        width: $card-heigh * 0.8;
        height: $card-heigh * 0.8;
        margin: $card-heigh * 0.1;
        background-repeat: no-repeat;
        background-size: 100% 100%;
      }
      .content {
        flex: 1;
        .pinnedArtilce {
          position: absolute;
          // display: block;
          // border-bottom: 1px #000 solid;
          right: 15px;
          top: 10px;
          color: red;
          font-size: 12px;
          i {
            margin-right: 3px;
          }
        }
      }
      .title {
        color: #21bfd4;
        margin: 20px 0 10px 0;
        cursor: pointer;
      }
      .title:hover {
        text-decoration: underline;
      }
      .detail {
        height: 0.4 * $card-heigh;
        display: -webkit-box;
        overflow: hidden;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        color: rgb(133, 125, 125);
        font-size: 0.8em;
        line-height: 2em;
        margin: 0 10px 10px 0;
        cursor: pointer;
      }
      .info {
        font-size: 0.8em;
        color: #857d7d;
        margin-bottom: 5px;
        span {
          margin-right: 20px;
        }
        span i {
          margin-right: 5px;
        }
        .tag {
          color: #e73da0;
          cursor: pointer;
        }

        .love {
          color: #6c6d6b;
          .heart {
            cursor: pointer;
          }
        }
        .views {
          color: #6c6d6b;
          // float: right;
        }
        .comment {
          color: #6c6d6b;
          cursor: pointer;
          // float: right;
        }
        .comment:hover {
          text-decoration: underline;
        }
        .fa-calendar {
          color: #21bfd4;
        }
      }
    }
  }
  .pagination {
    margin: 0 auto !important;
  }
}
</style>



