<template>
  <header>
    <nav :class="{ isArticle: (this.$route.path !== '/') }">
      <div class="nav-wrapper">
        <!-- <a href="#" class="brand-logo center logo">Sky的精神角落</a> -->
        <ul
          id="nav-mobile"
          class="right hide-on-med-and-down"
        >
          <li
            class="toHome"
            @click="goNext('home')"
          ><a>Home</a></li>
          <li
            class="toArticles"
            @click="goNext('article')"
          ><a>Articles</a></li>
          <li
            class="toProjects"
            @click="goNext('project')"
          ><a>Projects</a></li>
        </ul>
        <!-- <a href="#" data-target="slide-out" class="sidenav-trigger waves-effect">
          <i class="fa fa-bars"></i>
        </a> -->
        <button
          href="#"
          data-target="slide-out"
          class="sidenav-trigger waves-effect"
        >
          <span></span>
        </button>
      </div>
    </nav>
    <ul
      id="slide-out"
      class="sidenav"
    >
      <!-- <li>
        <div class="user-view">
          <div class="background">
            <img src="../assets/img/your2.jpg">
          </div>
          <a href="#user"><img class="circle" src="../assets/img/portrait.jpeg"></a>
          <a href="#name"><span class="white-text name">Sky Liu</span></a>
          <a href="#email"><span class="white-text email">liu_tianyi@outlook.com</span></a>
        </div>
      </li>
      <li><a href="#!"><i class="material-icons">cloud</i>First Link With Icon</a></li>
      <li><a href="#!">Second Link</a></li>
      <li>
        <div class="divider"></div>
      </li> -->
      <li class="toHome"><a
          class="waves-effect"
          href="#!"
          @click="goNext('home')"
        >Home</a></li>
      <li class="toArticles"><a
          class="waves-effect"
          href="#!"
          @click="goNext('article')"
        >Articles</a></li>
      <li class="toProjects"><a
          class="waves-effect"
          href="#!"
          @click="goNext('project')"
        >Projects</a></li>
      <li class=""><a
          class="waves-effect"
          href="#!"
        >About me</a></li>
    </ul>
  </header>
</template>

<script>
export default {
  methods: {
    goNext: function (e) {
      var speed = 500
      if (this.$route.path != '/') {
        this.$router.push('/')
        speed = 0
      }
      setTimeout(function () {
        if (e === 'home') {
          $('html , body').animate(
            { scrollTop: $('#welcome').offset().top - 50 },
            speed
          )
          $('.sidenav-overlay').click()
        }
        else if (e === 'article') {
          $('html , body').animate(
            { scrollTop: $('.articleList').offset().top - 280 },
            speed
          )
          $('.sidenav-overlay').click()
        }
        else if (e === 'project') {
          $('html , body').animate(
            { scrollTop: $('#projects').offset().top - 60 },
            speed
          )
          $('.sidenav-overlay').click()
        }
      }, 100)
    }
  },
  mounted () {
    $('.sidenav').sidenav()
    $('.sidenav-trigger').click(function () {
      $(this).addClass('is-active')
    })
    $('.sidenav-overlay,#slide-out').click(function () {
      $('.sidenav-trigger').removeClass('is-active')
    })
  }
}
</script>

<style lang="scss" scoped>
@media only screen and (min-width: 992px) {
  .sidenav-trigger {
    display: none;
  }
}
nav {
  position: fixed;
  box-shadow: none;
  background-color: transparent;
  z-index: 100;
  a i {
    font-size: 1.5em;
  }
}
.sidenav-trigger {
  width: 50px;
  height: 60px;
  position: relative;
  background: transparent;
  border: none;
  vertical-align: middle;
  padding: 18px 12px;
  margin: 0;
  cursor: pointer;
  &:focus {
    outline: 0;
  }
  &:hover {
    span,
    span:before,
    span:after {
      background: #880665;
    }
  }
  &:before {
    content: "";
    opacity: 0;
    width: 0;
    height: 0;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    background: transparent;
    transform: translate(-50%, -50%);
    transition: all 0.4s ease;
  }
  span {
    display: block;
    position: relative;
    &:before,
    &:after {
      content: "";
      position: absolute;
      left: 0;
    }
    &:before {
      top: -8px;
    }
    &:after {
      bottom: -8px;
    }
  }
  span,
  span:before,
  span:after {
    width: 100%;
    height: 4px;
    background: #f300b4;
    transition: all 0.4s ease;
  }
  &.is-active {
    &:before {
      opacity: 1;
      width: 33px;
      height: 33px;
      background: #fff;
    }
    span {
      background: transparent;
      &:before {
        top: 0;
        transform: rotate(225deg);
      }
      &:after {
        bottom: 0;
        transform: rotate(-225deg);
      }
    }
  }
}
.sidenav-overlay {
  cursor: pointer;
}
.sidenav {
  margin-top: 56px;
  height: 200px;
  width: 100%;
  padding-bottom: 0; // 重置materialize的默认60px下内边距
  background-color: #181d23;
  a,
  i {
    color: #fff;
  }
  img {
    width: 100%;
  }
}
.isArticle {
  background-color: #181d23 !important;
}
</style>



