<template>
  <div class="markdown-body">
    <h3> {{ article.title }}</h3>
    <div
      class="info"
      v-if="article"
    >
      <span class="date"><i class="fa fa-calendar"></i>{{article.date}}</span>
      <span
        class="tag"
        v-on:click="handleTag(article.tag)"
      ><i class="fa fa-tags"></i>{{article.tag}}</span>
      <!-- <span class="comment"><i class="fa fa-comment"></i>评论 ( {{article.comment}} )</span> -->
      <span class="views"><i class="fa fa-eye"></i>浏览 ( {{article.view}} )</span>
      <span class="loveThis">
        <span
          class="heart"
          :class="{'heartActive':(youHaveLoved.indexOf(article._id) != '-1')}"
          @click.once="loveArtilce(article,$event)"
        ></span>
        <span>喜欢 ( {{article.love}} )</span>
      </span>
    </div>
    <div
      v-html="article.content"
      v-highlight
    ></div>
    <div
      class="reward"
      @click="rewardArticle"
    >
      <span>打赏一个</span>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import hljs from 'highlight.js'
import 'highlight.js/styles/atom-one-dark.css'

export default {
  data () {
    return {
      article: '',
      youHaveLoved: JSON.parse(localStorage.getItem("youHaveLoved")) || [],
      isClick: true,
      timeout: 10, // 文章过期时间，分钟为单位
    }
  },
  methods: {
    getArticle () {
      var article = JSON.parse(sessionStorage.getItem("article")) || {}
      var startTime = JSON.parse(sessionStorage.getItem("articleTimeout")) || new Date().getTime()
      if (article._id == this.$route.params.id && new Date().getTime() - startTime < 1000 * 60 * this.timeout) {
        console.log('old')
        this.article = article
      } else {
        console.log('new')
        axios
          .get('/apis/getArticle/' + this.$route.params.id)
          .then(res => {
            this.article = res.data
            sessionStorage.setItem("article", JSON.stringify(res.data))
            sessionStorage.setItem("articleTimeout", JSON.stringify(new Date().getTime()))
          })
      }
    },
    loveArtilce: function (e, event) {
      if (this.youHaveLoved.indexOf(e._id) == '-1') {
        // 点击开关，当点击一次时开关关闭，在完成动画后开关打开，避免在动画执行期间重复点击
        e.love++
        $(event.target).addClass("heartAnimation")
        var that = this
        // 给点击动画预留的时间缓冲
        setTimeout(function () {
          that.youHaveLoved.push(e._id)
          axios
            .get('/apis/loveArtilce', {
              params: {
                id: e._id
              }
            })
          localStorage.setItem("youHaveLoved", JSON.stringify(that.youHaveLoved))
        }, 800)
      }
      else {
        // this.$message.error('调皮😝你已经点过赞啦～');
        this.$notify({
          title: '调皮😝',
          message: '你已经点过赞啦～',
          type: 'warning'
        });
      }
    },
    rewardArticle: function () {
      this.$alert("<img src='/images/reward.png' style='width:100%'>", '感谢你的支持😊', {
        dangerouslyUseHTMLString: true,
        showConfirmButton: false,
        callback: action => { }
      });
    },
    articleMinHeight: function () {
      var line = $(window).height() - $('footer').height() + 'px'
      $('.markdown-body').css('min-height', line)
    }
  },
  directives: {
    highlight: function (el) {
      let blocks = el.querySelectorAll('pre code')
      blocks.forEach((block) => {
        hljs.highlightBlock(block)
      })
    },
  },
  mounted () {
    this.getArticle()
    this.articleMinHeight()
    window.onresize = this.articleMinHeight
  }
}
</script>

<style lang="scss" scoped>
.markdown-body {
  position: relative;
  background-color: white;
  padding: 100px 10%;
  z-index: 1;
  .info {
    font-size: 0.8em;
    color: #857d7d;
    span {
      display: inline-block;
      margin-right: 10px;
      margin-bottom: 10px;
    }
    span i {
      margin-right: 3px;
    }
    .tag {
      color: #e73da0;
    }
    .loveThis {
      color: #6c6d6b;
      .heart {
        cursor: pointer;
      }
    }
    .views {
      color: #6c6d6b;
    }
    .comment {
      color: #6c6d6b;
      cursor: pointer;
    }
    .comment:hover {
      text-decoration: underline;
    }
    .fa-calendar {
      color: #21bfd4;
    }
  }
  .reward {
    font-size: 16px;
    font-weight: 900;
    cursor: pointer;
    color: #e73da0;
    margin-top: 50px;
    text-align: center;
    span {
      padding: 5px;
      border: 1px solid #e73da0;
      border-radius: 5px;
      transition: 300ms;
      &:hover {
        background-color: #e73da0;
        color: #fff;
        transition: 300ms;
      }
    }
  }
}
</style>



