<template>
  <div id="welcome">
    <section id="welcome-bg">
      <div class="welcome-bg-star"></div>
      <div class="welcome-bg-moon"></div>
      <div class="welcome-bg-forest"></div>
      <div class="welcome-bg-forest-border"></div>
      <div class="welcome-bg-man"></div>
      <div class="welcome-container">
        <!-- <h1> -->
        <span>Someone</span>
        <span class="color">Like</span>
        <span>You</span>
        <div class="welcome-buttons">
          <a
            class="btn-1"
            @click="goNext('article')"
          >
            My Articles
          </a>
          <a
            class="btn-2"
            @click="goNext('project')"
          >
            My Projects
          </a>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data () {
    return {
      he: $(window).height()
    }
  },
  methods: {
    resize: function () {
      $('#welcome-bg').css('height', $(window).height())
      $('.welcome-bg-forest-border').css({
        'border-left-width': $(window).width() * 0.2 + 'px',
        'border-right-width': $(window).width() * 0.8 + 'px'
      })
    },
    scroll: function () {
      // console.log($(window).height())
      if ($(window).scrollTop() > $(window).height() - $('nav').height()) {
        $('nav').css('background-color', '#181d23')
      } else {
        $('nav').css('background-color', 'transparent')
      }
    },
    goNext: function (e) {
      if (e === 'article') {
        $('html , body').animate(
          { scrollTop: $('#information-wrap').offset().top - 50 },
          500
        )
      }
      else if (e === 'project') {
        $('html , body').animate(
          { scrollTop: $('#projects').offset().top - 60 },
          500
        )
      }
    }
  },
  mounted () {
    this.resize() // 初始化需要js计算的宽高
    $(window)
      .on('resize', this.resize)
      .on('scroll', this.scroll)
  }
}
</script>
<style lang="scss" scoped>
@media only screen and (max-width: 700px) {
  .welcome-container {
    top: 15% !important;
    left: 50%;
    transform: translateX(-50%);
  }
  .welcome-bg-man {
    left: 50%;
    transform: translateX(-50%);
  }
}

#welcome {
  .background img {
    width: 100%;
  }
  #welcome-bg {
    position: relative;
    background: #202736;
    // height: 2000px; // 默认高度用于onload之前填充高度，否则高度为0，index会首先显示
    background: linear-gradient(to bottom, #181d23 0%, #202736 80%);
    background-attachment: fixed;
    box-sizing: border-box;
    z-index: 1;
    .welcome-bg-star {
      position: fixed;
      background: url("../../assets/img/welcome/Stars.svg");
      width: 100%;
      height: 100%;
      opacity: 0;
      animation: stars-move-in 1000ms 300ms forwards;
    }
    .welcome-bg-moon {
      position: absolute;
      background: url("../../assets/img/welcome/Moon.svg") no-repeat;
      width: 35%;
      height: 35%;
      right: 3%;
      top: 5%;
      z-index: 1;
      opacity: 0;
      animation: moon-move-in 1.2s 1s forwards;
    }
    .welcome-bg-man {
      position: absolute;
      // left: 20%;
      bottom: 0;
      opacity: 0;
      background: url("../../assets/img/welcome/Silhouette.svg") no-repeat;
      width: 50%;
      height: 50%;
      animation: man-move-in 1000ms 800ms forwards;
    }
    .welcome-bg-forest {
      position: absolute;
      width: 100%;
      height: 68%;
      background: url("../../assets/img/welcome/Trees.svg");
      bottom: 0;
      left: 0;
      background-size: cover;
      opacity: 0;
      animation: forest-move-in 1000ms 500ms forwards;
    }
    .welcome-bg-forest-border {
      position: absolute;
      bottom: -200px;
      left: 0px;
      padding: 0;
      // background: url('../../assets/img/welcome/Moon.svg') no-repeat;
      border-bottom: 100px solid #f6f6f6;
      border-top: 100px solid #181d23;
      border-left: solid #f6f6f6;
      border-right: solid #f6f6f6;
      // margin-bottom: 100px;
      display: block;
      content: "";
      z-index: 1;
    }
    .welcome-container {
      position: absolute;
      display: inline-block;
      width: fit-content;
      top: 40%;
      right: 40%;
      // right: 0;
      // left: 0;
      color: #f6f6f6;
      opacity: 0;
      animation: text-fade-in 1000ms 800ms forwards;
      .welcome-buttons {
        margin-top: 20px;
        a {
          display: inline-block;
          width: 150px;
          border: 1px #f6f6f6 solid;
          padding: 4% 0;
          text-transform: uppercase;
          text-align: center;
          color: #f6f6f6;
          cursor: pointer;
          transition: 200ms;
          &:first-child {
            margin-right: 10px;
          }
          &:nth-child(2) {
            border: 1px #f300b4 solid;
            background-color: #f300b4;
          }
          &:hover {
            border: 1px #f300b4 solid;
            box-shadow: 0 10px 100px -20px #f300b4;
            background-color: transparent;
            transform: scale(1.06);
            transition: 200ms;
          }
        }
      }
      span {
        display: block;
        font-size: 50px;
        line-height: 60px;
        font-style: italic;
        font-weight: 600;
        &[class="color"] {
          color: #f300b4;
        }
      }
    }
  }
}
@keyframes moon-move-in {
  from {
    background-position: right 150%;
  }
  to {
    opacity: 1;
    // background-position: top right;
  }
}
@keyframes forest-move-in {
  from {
    background-position-y: 150%;
  }
  to {
    opacity: 1;
    background-position-y: 100%;
  }
}
@keyframes stars-move-in {
  from {
    background-position-y: -100px;
  }
  to {
    opacity: 1;
    background-position-y: 0;
  }
}
@keyframes man-move-in {
  from {
    background-position-x: 0;
  }
  to {
    opacity: 1;
    background-position-x: 50%;
  }
}
@keyframes text-fade-in {
  from {
    right: 0;
  }
  to {
    opacity: 1;
    right: 25%;
  }
}
</style>



