<template>
  <section id="information-wrap">
    <!--信息流模块 -->
    <div id="information">
      <!-- <carousel></carousel> -->
      <articleList></articleList>
    </div>
    <div id="dashboard">
      <clork size="130" class='clork'></clork>
      <me></me>
      <!-- <weather></weather> -->
    </div>
  </section>
</template>

<script>
import articleList from '@/components/article/articleList'
import clork from '@/components/common/clork'
import carousel from '@/components/common/carousel'
import me from '@/components/common/about-me'
import viewers from '@/components/common/viewers'
import weather from '@/components/common/weather'

export default {
  components: {
    clork,
    me,
    viewers,
    weather,
    carousel,
    articleList
  }
}
</script>

<style lang="scss" scoped>
@media only screen and (min-width: 1240px) {
  #information-wrap {
    padding: 30px 20%;
  }
}
@media only screen and (min-width: 1070px) and (max-width: 1239px) {
  #information-wrap {
    padding: 30px 20%;
  }
  #dashboard {
    display: none;
  }
}
@media only screen and (max-width: 1069px) {
  #information-wrap {
    padding: 30px 0 !important;
  }
  #dashboard {
    display: none;
  }
  .article-wrap {
    margin: 0 auto;
  }
  .articleList {
    padding: 0 10px;
  }
}
#information-wrap {
  position: relative;
  display: flex;
  margin: 150px 0;
  background-color: #f6f6f6;
  z-index: 2;
  #dashboard {
    flex: 2;
    padding-left: 20px;
    .clork {
      text-align: center;
      margin: 10px 0;
    }
  }
  #information {
    flex: 3;
  }
}
</style>



