<template>
  <section class="carousel">
    <div class="carousel-left">
      <el-carousel :interval="5000">
        <el-carousel-item v-for="item in 4" :key="item">
          <!-- <h3>{{ item }}</h3> -->
          <img src="../../assets/img/your.jpg">
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="carousel-right">
      <div class="carousel-right-item-1">2</div>
      <div class="carousel-right-item-2">3</div>
    </div>
  </section>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {}
  },
  methods: {
    carouselResize: function () {
      if ($(window).width() < 1069) {
        $(
          '.carousel,.el-carousel,.el-carousel__item,.el-carousel__container'
        ).css({
          width: $(window).width(),
          height: ($(window).width() * 9) / 16
        })
        $('.carousel-left img').css({ height: '100%' })
      } else {
        $('.carousel').css({ width: '700px', height: '262.5px' })
        $('.el-carousel,.el-carousel__item,.el-carousel__container').css({
          width: '466.672px'
        })
        $('.carousel-left img').css({ height: '262.5px' })
      }
    }
  },
  mounted () {
    this.carouselResize()
    $(window).on('resize', this.carouselResize)
  }
}
</script>

<style scoped>
@media only screen and (max-width: 1069px) {
  .carousel-right {
    display: none;
  }
}
.classxs {
  width: '$(window).width()';
  height: '($(window).width() * 9) / 16';
}
.carousel {
  display: flex;
  height: 262.5px;
  width: 700px;
}
.carousel-left {
  flex: 2;
  background-color: rgba(234, 234, 243, 0.541);
}
.carousel-left img {
  width: 100%;
  height: 100%;
}
.carousel-right {
  flex: 1;
}
/* .carousel-right-xs {
    display: none !important;
} */
.carousel-right-item-1 {
  background-color: red;
  height: 150px;
}
.carousel-right-item-2 {
  background-color: yellow;
  height: 150px;
}
</style>



