<template>
  <footer class="page-footer">
    <!-- 页脚开始 -->
    <div class="container">
      <div class="row">
        <div class="col l5 s12">
          <h5 class="white-text">WELCOME</h5>
          <p class="grey-text text-lighten-4">你是今天第{{ index }}位到这儿来的胖友，超过了99%的人类！真腻害~</p>
        </div>
        <div class="col l4 s12">
          <h5 class="white-text">友情链接</h5>
          <ul>
            <li>
              <a
                class="grey-text text-lighten-3"
                href="https://mobike.com/cn/"
              >摩拜科技</a>
            </li>
            <li>
              <a
                class="grey-text text-lighten-3"
                href="http://www.qyer.com/"
              >穷游网</a>
            </li>
            <li>
              <a
                class="grey-text text-lighten-3"
                href="http://graduate.buct.edu.cn/"
              >北京化工大学研究生院</a>
            </li>
            <li>
              <a
                class="grey-text text-lighten-3"
                href=""
              >广告位招租</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="footer-copyright">
      <div class="container">
        <a class="grey-text text-lighten-4 left">© 2018 GoodSky , Inc.</a>
        <a class="grey-text text-lighten-4 right">今日访问量: {{ todayPV }}</a>
      </div>
    </div>
  </footer>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      todayPV: '',
      index: ''
    }
  },
  mounted () {
    axios
      .get('/apis/vistor')
      .then(res => {
        this.todayPV = res.data.pv
        this.index = res.data.index
      })
  }
}
</script>

<style lang="scss" scoped>
footer {
  position: relative;
  background-color: #181d23;
  z-index: 1;
  a,
  p {
    line-height: 1.5em;
  }
}
</style>



