import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/main/main'
import Article from '@/components/article/article'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'main',
      component: Main,
      children: [
        {
          path: '/:id',
          redirect: '/'
        }
      ]
    },
    {
      path: '/article/:id',
      name: 'article',
      component: Article,
      children: []
    }
  ]
})



